import React, { Component, useMemo } from 'react';
import moment from 'moment';
import { requestHeaderGETWithAuth, requestHeaderPUTWithAuth, requestHeaderPOSTWithAuth } from 'src/Rest-API/Header';
import { getUserDataFromLocalStorage, logoutProcedureForLocalStorage } from 'src/Utils/AuthRelatedThings';
import { Logger, getSearchResult, openPageInNewTab, reloadPage, goHome } from '../../Utils/Utils';
import { Loading } from '../Statuses/Statuses';
import Table from 'react-bootstrap/Table';
import {
    CButton,
    CCard,
    CCardBody,
    CCardGroup,
    CCol,
    CContainer,
    CForm,
    CInput,
    CInputGroup,
    CInputGroupPrepend,
    CInputGroupText,
    CRow,
    CTextarea
  } from '@coreui/react';
import CIcon from '@coreui/icons-react';
import StatusModal from '../Modals/StatusModal';
import FailureModal from '../Modals/FailureModal';
import { brandSet } from '@coreui/icons';
import MaterialReactTable from 'material-react-table';
import { BACKEND_PRICE_TRACKER_SERVICE_BASE_URL } from 'src/Rest-API/EnvironmentSpecificThings';

//default
const FILE_NAME = "Brand.js";

//default
const initialState = {
    isLoaded: false,
    loadingModalVisible: false,
    loadingModalState: 0,
    failureModalVisible: false,
    brandListData: []
};

const tableColumnData = [
    {
        accessorKey: 'id',
        header: 'ID',
        enableEditing: false,
    },
    {
        accessorKey: 'name',
        header: 'Name',
        enableEditing: true,
    },
    {
        accessorKey: 'holdingCompany',
        header: 'Holding Company',
        enableEditing: true,
    },
    {
        accessorKey: 'officialBrandLink',
        header: 'Official Brand Link',
        enableEditing: true,
    },
    {
        accessorKey: 'createdAt',
        header: 'Created At',
        enableEditing: false,
        Cell: ({ renderedCellValue, row }) => (moment(renderedCellValue).zone('IST').format('YYYY/MM/DD HH:mm:ss ddd'))
    },
    {
        accessorKey: 'updatedAt',
        header: 'Updated At',
        enableEditing: false,
        Cell: ({ renderedCellValue, row }) => (moment(renderedCellValue).zone('IST').format('YYYY/MM/DD HH:mm:ss ddd'))
    },
];

class Brand extends Component {

    //default
    constructor(props) {
        super(props);
        this.state = JSON.parse(JSON.stringify(initialState));
    }

    //default
    resetStateToDefault() {
        this.setState(JSON.parse(JSON.stringify(initialState)));
    }

    //default
    reloadData() {
        this.resetStateToDefault();
        this.fetchBrandDataList();
    }

    //default
    componentDidMount() {
        document.title = "Expenses - Brand";
        this.fetchBrandDataList();
    }

    fetchBrandDataList() {
        let API_URL = BACKEND_PRICE_TRACKER_SERVICE_BASE_URL + '/brand/list?offset=0&from=admin-portal';
        const userData = getUserDataFromLocalStorage();

        fetch(API_URL, requestHeaderGETWithAuth(userData.authToken))
        .then(response => {
            if(response.status === 200)
                return response.json();
            if(response.status === 401) {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
                setTimeout(() => {
                    logoutProcedureForLocalStorage();
                    reloadPage();
                }, 500);
            } else {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
            throw new Error("HTTP status is " + response.status);
        })
        .then(
            (result) => {
                Logger(FILE_NAME, "fetchBrandDataList", "FETCH_1", "result", result);
                if(result.code === 1001) {
                    //success
                    this.setState({
                        isLoaded: true,
                        brandListData: result.data
                    });
                } else {
                    //something horrible happened
                    this.setState({
                        loadingModalVisible: false,
                        loadingModalState: 0,
                        failureModalVisible: true
                    });
                }
            },
            (error) => {
                Logger(FILE_NAME, "fetchBrandDataList", "FETCH_1", "error", error);
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
        );
    }
    
    handleSaveRowEdits(exitEditingMode) {
        Logger(FILE_NAME, "handleSaveRowEdits", "", "exitEditingMode", exitEditingMode);
        this.setState({
            loadingModalVisible: true,
            loadingModalState: 0
        });
        this.callBrandUpdateApi(exitEditingMode);
    }

    callBrandUpdateApi(exitEditingMode) {
        let API_URL = BACKEND_PRICE_TRACKER_SERVICE_BASE_URL + '/brand/update';
        const userData = getUserDataFromLocalStorage();

        fetch(API_URL, requestHeaderPUTWithAuth(JSON.stringify(exitEditingMode.values), userData.authToken))
        .then(response => {
            if(response.status === 200)
                return response.json();
            if(response.status === 401) {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
                setTimeout(() => {
                    logoutProcedureForLocalStorage();
                    reloadPage();
                }, 500);
            } else {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
            throw new Error("HTTP status is " + response.status);
        })
        .then(
            (result) => {
                Logger(FILE_NAME, "callBrandUpdateApi", "FETCH_1", "result", result);
                if(result.code === 1001) {
                    //success
                    this.setState({
                        loadingModalState: 1,
                    });

                    setTimeout(() => {
                        this.setState({
                            loadingModalVisible: false,
                            loadingModalState: 0
                        });
                        exitEditingMode.exitEditingMode();
                        this.reloadData();
                    }, 500);
                    
                } else {
                    //something horrible happened
                    this.setState({
                        loadingModalVisible: false,
                        loadingModalState: 0,
                        failureModalVisible: true
                    });
                }
            },
            (error) => {
                Logger(FILE_NAME, "callBrandUpdateApi", "FETCH_1", "error", error);
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
        );

    }

    submitCreateBrandForm = (event) => {
        event.preventDefault();

        let reqData = {
            name: event.target.inputName.value,
            holdingCompany: event.target.inputHoldingCompany.value,
            officialBrandLink: event.target.inputOfficialBrandLink.value
        };

        this.setState({
            loadingModalVisible: true,
            loadingModalState: 0
        });

        this.callCreateBrandApi(reqData);
    }

    callCreateBrandApi(newBrand) {
        let API_URL = BACKEND_PRICE_TRACKER_SERVICE_BASE_URL + '/brand/save?offset=0&from=admin-portal';
        const userData = getUserDataFromLocalStorage();
        fetch(API_URL, requestHeaderPOSTWithAuth(JSON.stringify(newBrand), userData.authToken))
        .then(response => {
            if(response.status === 200)
                return response.json();
            if(response.status === 401) {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
                setTimeout(() => {
                    logoutProcedureForLocalStorage();
                    reloadPage();
                }, 500);
            } else {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
            throw new Error("HTTP status is " + response.status);
        })
        .then(
            (result) => {
                Logger(FILE_NAME, "submitCreateBrandForm", "FETCH_1", "result", result);
                if(result.code === 1001) {
                    //success
                    this.setState({
                        loadingModalState: 1,
                    });
                    setTimeout(() => {this.reloadData();}, 200);
                } else {
                    //something horrible happened
                    this.setState({
                        loadingModalVisible: false,
                        loadingModalState: 0,
                        failureModalVisible: true
                    });
                }
            },
            (error) => {
                Logger(FILE_NAME, "submitCreateBrandForm", "FETCH_1", "error", error);
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
        );
    }


    //default
    render() {
        //default
        Logger(FILE_NAME, "render", "", "this.state", this.state);

        return (            
                this.state.isLoaded ? 
                <div>
                    <h1>BRAND &nbsp;&nbsp;&nbsp;&nbsp;&nbsp; 
                        <CButton color="success" className="px-4" onClick={() => this.reloadData()}>Reload Data</CButton>
                    </h1>
                    <br />

                    {
                    this.state.isLoaded ?
                        <MaterialReactTable
                            initialState={{ density: 'compact' }}
                            columns={tableColumnData} 
                            data={this.state.brandListData}
                            enablePagination = {true}

                            editingMode={"row"}
                            enableEditing
                            onEditingRowSave={(exitEditingMode) => this.handleSaveRowEdits(exitEditingMode)}
                        /> : <br/>
                    }
                    <br /><br />
                    {
                        /* Create new brand */
                        <CContainer>
                        <CRow className="justify-content-center">
                        <CCol md="8">
                            <CCardGroup>
                            <CCard className="p-4">
                                <CCardBody>
                                <CForm onSubmit={this.submitCreateBrandForm}>
                                    <h1>Save a new brand</h1>
                                    <CInputGroup className="mb-3">
                                        <CInputGroupPrepend>
                                            <CInputGroupText>
                                            <CIcon name="cilPencil" />
                                            </CInputGroupText>
                                        </CInputGroupPrepend>
                                        <CInput
                                            placeholder="Brand Name" 
                                            autoComplete="on"
                                            type="text"
                                            step="any"
                                            id="inputName"
                                            label="Brand Name"
                                            required
                                        />
                                    </CInputGroup>
                                    <CInputGroup className="mb-3">
                                        <CInputGroupPrepend>
                                            <CInputGroupText>
                                            <CIcon name="cilPencil" />
                                            </CInputGroupText>
                                        </CInputGroupPrepend>
                                        <CInput
                                            placeholder="Holding company" 
                                            autoComplete="on"
                                            type="text"
                                            step="any"
                                            id="inputHoldingCompany"
                                            label="Holding company"
                                            required
                                        />
                                    </CInputGroup>
                                    <CInputGroup className="mb-3">
                                        <CInputGroupPrepend>
                                            <CInputGroupText>
                                            <CIcon name="cilPencil" />
                                            </CInputGroupText>
                                        </CInputGroupPrepend>
                                        <CInput
                                            placeholder="Official Brand Link" 
                                            autoComplete="on"
                                            type="text"
                                            step="any"
                                            id="inputOfficialBrandLink"
                                            label="Official Brand Link"
                                            required
                                        />
                                    </CInputGroup>
                                    

                                    <CRow>
                                        <CCol xs="6">
                                            <CButton color="success" className="px-4" type='submit'>SAVE</CButton>
                                        </CCol>
                                        <CCol xs="6" className="text-right">
                                            <CButton color="danger" className="px-0" onClick={() => this.reloadData()}>&nbsp;Reset values&nbsp;</CButton>
                                        </CCol>
                                    </CRow>
                                </CForm>
                                </CCardBody>
                            </CCard>
                            </CCardGroup>
                        </CCol>
                        </CRow>
                        </CContainer>      
                    }

                    {/* //default */}
                    <StatusModal visible={this.state.loadingModalVisible} loadingModalState={this.state.loadingModalState} />
                    <FailureModal visible={this.state.failureModalVisible} />
                </div>
                : <Loading />
        );
    }
}

export default Brand;
